* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #1a2036;
}
a {
  text-decoration: none;
}
.app {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "AbelRegular";
}

.video {
  position: fixed;
  z-index: -1;
  width: 100%;
  left: 50%;
  top: 50%;
  height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  display: flex;
}

.main-wrapper {
  border-radius: 3rem;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  max-width: 950px;
  min-width: 850px;
  width: 62vw;
  height: 80vh;
  background-color: #242b42ab;
  position: relative;
}

.my-name {
  position: absolute;
  right: 10px;
  bottom: 5px;
  color: white;
}

/* "mobile" from 800px */
@media only screen and (max-width: 890px) {
  .app {
    height: auto;
    background-image: url("../../images/mobile.png");
    background-attachment: fixed;
    background-position: top;
  }
  .main-wrapper {
    height: 100%;
    width: 100%;
    display: block;
    border-radius: 0;
    max-width: initial;
    min-width: initial;
    /* overflow: initial; */
    background-color: transparent;
  }
  #root {
    overflow-x: hidden;
  }
}
